*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  margin: 0 auto;
  width: 1250px;
  max-width: 100%;
}
.main-form{
  display: flex;
  justify-content: center;
}
.form{
  margin-top: 20px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .30);
  width: 400px;
}
.form input{
  border: none  ;
  background-color: rgba(0, 0, 0, 0);
  display: block;
  width: 100%;
  font-size: 16;
  margin-bottom: 5px;
}
.form textarea{
  border: none  ;
  background-color: rgba(0, 0, 0, 0);
  display: block;
  width: 100%;
}
.form textarea, input:focus{
  outline: none;
}
.btn{
  display: block;
  float: right;
  padding: 15px 5px !important;
  border-radius: 100% !important;
  /* margin-top: 10px !important; */
  position: absolute;
  top: 35px;
}
.notes{
  width: 24.5%;
  float: left;
  max-width: 100%;
  box-shadow: 0 0 5px rgba(0, 0, 0, .30);
  padding: 10px;
  margin-top: 40px;
  margin-left: 6px;
}
.btn1{
  display: block;
  float: right;
  padding: 15px 5px !important;
  border-radius: 50% !important;
  margin-top: 10px !important;
}